  border-radius: 30px;
  background-color: #D8FAFC;
  padding: 30px;
  transition: all 0.1s ease-out;
  cursor: pointer;
}
.advantages .cards .card:hover {
  background-color: #9EE6EA;
  scale: 105%;
}
.advantages .cards .card img {
  height: 60px;
  margin-bottom: 12px;
}
.advantages .cards .card .title {
  font-family: "Baloo 2", sans-serif;
  font-weight: 700;
