}
.about .boxes .box.left.first {
  padding-left: 170px;
}
.about .boxes .box.left.second {
  padding-left: 250px;
}
.about .boxes .box.left.third {
  padding-left: 170px;
}
.about .boxes .box:hover img {
  scale: 110%;
