/* LIGHTBOX */
#lightbox {
  position: fixed;
  z-index: 1000;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.85);
  display: none;
  top: 0;
  left: 0;
}
#lightbox.active {
  display: flex;
  justify-content: center;
  align-items: center;
}
#lightbox .lightbox-inner {
  position: relative;
  width: 80vw;
  height: 95vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
#lightbox .lightbox-inner .img-wrapper {
  position: relative;
  width: 60vw;
  height: 100%;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}
#lightbox .lightbox-inner .img-wrapper > img {
  height: 100%;
  object-fit: contain;
  width: 100%;
}
#lightbox .lightbox-inner .close-btn {
  position: absolute;
  top: 0px;
  right: 0px;
  padding: 10px;
  width: 80px;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
#lightbox .lightbox-inner .close-btn svg {
  width: 80px;
  fill: #BD8E22;
  opacity: 0.5;
}
#lightbox .lightbox-inner .close-btn:hover svg {
  opacity: 1;
}
#lightbox .lightbox-inner .prev-btn {
  position: absolute;
  top: 50%;
  left: 0cm;
  padding: 30px;
  transform: translateY(-50%);
  cursor: pointer;
}
#lightbox .lightbox-inner .prev-btn svg {
  width: 80px;
  fill: #BD8E22;
  opacity: 0.5;
}
#lightbox .lightbox-inner .prev-btn:hover svg {
  opacity: 1;
}
#lightbox .lightbox-inner .next-btn {
  position: absolute;
  top: 50%;
  right: 0;
  padding: 30px;
  transform: translateY(-50%);
  cursor: pointer;
}
#lightbox .lightbox-inner .next-btn svg {
  width: 80px;
  fill: #BD8E22;
  opacity: 0.5;
}
#lightbox .lightbox-inner .next-btn:hover svg {
  opacity: 1;
}
#lightbox .lightbox-inner .prev-btn.disabled,
#lightbox .lightbox-inner .next-btn.disabled {
  opacity: 0.1;
}

